.container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    flex-direction: column;
}

.cardView {
    height: 427px;
    width: 347px;
    background: #F4F4F4;
    border-radius: 25px;
    padding: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;
}

.input {
    background: #f4f4f4;
    border-radius: 5px;
    margin-bottom: 30px;
    color: #B0B0B0;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 20.5156px;
}

.input fieldset {
    border: 0px
}

.input label {
    color: #B0B0B0;
}

button.submit {
    background-color: #adda26;
    padding: 10px 20px;
    border-radius: 10px;
    color: #FFFFFF;
    font-weight: 400;
    box-shadow: 2px 2px 4px rgb(0 0 0 / 25%);
    text-transform: none;
    width: 137px;
    height: 44.69px;
    font-size: 18px;
}

button.submit:hover {
    background-color: #adda26;
    color: #FFFFFF;
    box-shadow: 4px 4px 6px rgb(0 0 0 / 25%);
}

.logo {
    width: 160px;
}

@media only screen and (max-width: 600px) {
    .cardView {
        padding: 20px;
        height: 413px;
        width: 80%;
        border-radius: 14px;
    }

    button.submit {
        width: 81px;
        height: 30px;
        border-radius: 5px;
        font-size: 11px;
    }

}

@media only screen and (min-width: 600px) and (max-width: 1024px){
    .cardView {
        width: 333px;
        height: 433px;
        border-radius: 14px;
    }

    button.submit {
        width: 91px;
        height: 35px;
        border-radius: 5px;
        font-size: 12px;
        margin-top: 10px;
    }
}